import React, {Component} from 'react';
import { Rate } from 'antd';
import { Input, Carousel} from 'antd';
import styles from './index.css';

const Search = Input.Search;

class Home extends Component {
    render(){
        return(
            <div className={styles.root}>
                <Search
                    placeholder="搜索美食"
                    onSearch={value => console.log(value)}
                    enterButton="搜索"
                />
                <div>
                    <h2 className={styles['home-title']}>精品推荐</h2>
                    <Carousel autoplay>
                        <div>
                            <img src="https://cp1.douguo.com/upload/caiku/c/4/a/650x366_c4a031fbad6e53690cfd753b020994ba.jpg"></img>
                        </div>
                        <div>
                            <img src="https://cp1.douguo.com/upload/caiku/9/6/5/650x366_96e18b047503ea539bd868c9f9440005.jpg"></img>
                        </div>
                        <div>
                            <img src="https://cp1.douguo.com/upload/caiku/8/9/8/650x366_89c81c38a29c4aa9f6e9020ad1fa7988.jpg"></img>
                        </div>
                        <div> 
                            <img src="https://cp1.douguo.com/upload/caiku/e/f/7/650x366_efc15af157be63b71cfe5bfb0f2da447.jpg"></img>
                        </div>
                    </Carousel>
                </div>
            </div>
        )
    }
}

export default Home;